import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';

@Component({
  selector: 'app-register-consumer',
  templateUrl: './register-consumer.page.html',
  styleUrls: ['./register-consumer.page.scss'],
})
export class RegisterConsumerPage implements OnInit {

  first_name: string = "";
  last_name: string = "";
  contact_number: any = "";
  street: string = "";
  barangay_name: string = "";
  username: string = "";
  password: string = "";
  agree = false;

  disabledButton;

  constructor(
    private router: Router,
    private toastController: ToastController,
    private loadingController: LoadingController,
    private alertController: AlertController,
    private accessProviders: AccessProviders
  ) { }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.disabledButton = false;
  }

  async tryRegister() {
    if(this.first_name == "") {
      this.presentToast('Incomplete information!');
    } else if(this.last_name == "") {
      this.presentToast('Incomplete information!');
    } else if(this.contact_number == "") {
      this.presentToast('Incomplete information!');
    } else if(this.street == "") {
      this.presentToast('Incomplete information!');
    } else if(this.barangay_name == "") {
      this.presentToast('Incomplete information!');
    } else if(this.username == "") {
      this.presentToast('Incomplete information!');
    } else if(this.password == "") {
      this.presentToast('Incomplete information!');
    }else if(this.agree == false){
      this.presentToast("Confirmation for Terms and Condition is required");
    } else {
      this.disabledButton = true;
      

      return new Promise(resolve => {
        let body = {
          aksi: 'process_consumer',
          first_name: this.first_name,
          last_name: this.last_name,
          contact_number: this.contact_number,
          street: this.street,
          barangay_name: this.barangay_name,
          username: this.username,
          password: this.password
        }
        this.accessProviders.postData(body, 'process_consumer.php').subscribe((res:any) => {
          if(res.success == true) {
            this.disabledButton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/main']);
          } else {
            this.disabledButton = false;
            this.presentToast(res.msg);
          }
        }, (err) => {
          this.disabledButton = false;
          this.presentAlert('Timeout');
        });
      });
    }
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }

  async presentAlert(a) {
    const alert = await this.alertController.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });
  }
}
